import { corvidEntities } from '@wix/motion-edm-autogen-transformations-core';
import { isArray, isPlainObject, mapKeys, transform } from 'lodash';

const { fromCorvidName, toCorvidName } = corvidEntities;

const renameUsing = (renameKey) => (object) =>
  mapKeys(object, (_, k) => renameKey(k));

const mapObjectDeep = (object, renameObjectKeys) =>
  renameObjectKeys(
    transform(object, (acc, value, key) => {
      // eslint-disable-next-line no-use-before-define
      acc[key] = mapDeep(value, renameObjectKeys);
    }),
  );

const mapDeep = (payload, renameObjectKeys) => {
  if (isPlainObject(payload)) {
    return mapObjectDeep(payload, renameObjectKeys);
  }

  if (isArray(payload)) {
    return payload.map((item) => mapDeep(item, renameObjectKeys));
  }

  return payload;
};

const fromCorvidEntity = (payload) =>
  mapDeep(payload, renameUsing(fromCorvidName));
const toCorvidEntity = (payload) => mapDeep(payload, renameUsing(toCorvidName));

export { fromCorvidEntity, toCorvidEntity };
